<template>
	<view class="content">
		<view class="contentCons">
			<view class='content_activity'>
				<view class="activity_content flex-column">
							<view v-for="(item,index) in ListtableData" v-bind:key="index" class="action flex-row top" @click.stop="openinfo('/pages/chart/analysis?id='+item.id)">
								<image class='activity_img' :src='item.img' :lazy-load='true'>
								<view class='activity_content_span flex-column' >
									<span class='title'>{{item.title}}</span>
									<span class='activity_contents'>{{item.content}}</span>
									<span class='price'>{{item.price}}</span>
								</view>
							</view>
						<u-gap height="60" bg-color="#f7f7f7"></u-gap>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		data() {
			return {
				ListtableData:[],
				downOption: {},
				upOption: {
					noMoreSize: 5
				},
				hxt:[],
			}
		},
		onLoad(){
			this.init()
		},
		methods: {
			init(){
				this.AkxhHdlists();
			},
			openinfo(url) {
			    uni.navigateTo({
			     url: url,
			    });
			},
			AkxhHdlists(){
				//户型图列表
				AkxhHd('/catalyzer/AkxhHx/page','GET',{column:'createTime',order:'ASC'}).then(res => {
					if (res.data.code == 200) {
						let result = res.data.result.records;
						let arr = [];
						result.forEach((item2,index2)=>{
							item2.img = this.imgURL + item2.img;
							arr.push(item2);
						});
						this.ListtableData = arr;
					}
				})
			},
		},
	}
</script>

<style>
	.flex-row {
	  display: flex;
	  justify-content: space-between;
	}
	.contentCons{
		background-color: #fff;
		width:100vw;
		color:#fff;
		position: absolute;
	}
	.content_activity{
		width: 100vw;
	}
	.content_activity .activity_title span{
		font-size: 18px;
		color:#fff;
		padding: 5px 15px;
		font-weight: 600;
		width: 70%;
	}
	.content_activity .activity_title button{
		font-size:11px;
		color:#f0f0f0;
	}
	.content_activity .activity_content{
		width: 100%;
	}
	.content_activity .activity_content .action{
		 border-bottom: #cbcbcb dotted 1px;
	}
	.content_activity .activity_content .activity_img{
		padding: 10px;
		width: 40%;
		height: 140px;
	}
	.content_activity .activity_content .activity_content_span{
		width: 55%;
	}
	.content_activity .activity_content .activity_content_span span{
		padding: 8px;
	}
	.activity_content_span .title{
		font-size: 18px;
		font-weight: 600;
		color:#000000;
	}
	.activity_content_span .activity_contents{
		font-size: 10px;
		color:#4d4d4d;
	}
	.activity_content_8d_img{
		width: 100%;
		height: 880px;
	}
	.content {
		display: flex;
	}
</style>
